<template>
    <div id="posts">
        <h1 class="text-center main-title">{{postMsg}}</h1>
        <div class="row">
            <div class="col-md-6" v-for="post in posts">
                <h1>{{post.title.rendered}}</h1>
                <img :src="post.image_banner">
                <p v-html="post.excerpt.rendered"></p>
                <a :href="slugPre + post.slug" class="btn btn-primary"><i class="fa fa-caret-right"></i>Read more</a>
            </div>
        </div>
    </div>
</template>

<script>
import Axios from 'axios'

export default {
    name: 'posts-comp',
    data: () => ({
        postMsg: 'Yeah! I have some posts!',
        posts: [],
        errors: [],
        slugPre: '/blog/'
    }),
    created () {
        Axios.get('https://ricardoamb.000webhostapp.com/wp-json/wp/v2/posts?per_page=8')
            .then(response => {
                // JSON responses are automatically parsed.
                this.posts = response.data
            })
            .catch(e => {
                //this.errors.push(e)
            })
    }
}
</script>

<style scoped>
    .main-title {
        color:#FFF;
        font-family: 'Roboto', sans-serif;
        font-weight: 300;
        background: lightgreen;
        padding: 10px;
    }
    img {
        max-width: 100%;
        margin-bottom: 20px;
    }
</style>


